<template>
  <section id="Certificados">
    <v-card color="#00000063" elevation="12" dark style="margin: 30px 0">
      <v-row>
        <v-col cols="12">
          <div style="font-size: 24px">Certificados</div>
          <v-row
            class="bg-1 centrar-flex"
            style="margin: 20px 60px; padding: 20px"
          >
            <v-col
              v-for="(item, i) in certificados"
              :key="item.nombre + i"
              cols="auto"
            >
              <span>{{ item.nombre }}</span>
              <div>
                <v-img
                  :src="item.url"
                  height="200px"
                  width="150px"
                  contain
                  class="ver-imagen"
                  @click="ver_certificado(i)"
                />
              </div>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
      <div style="padding-bottom: 30px" />
    </v-card>
    <v-dialog v-model="zoom" transition="dialog-transition">
      <v-card color="transparent">
        <v-img :src="url_zoom"/>
      </v-card>
    </v-dialog>
  </section>
</template>

<script>
export default {
  name: "CertificadosFragment",
  data: () => ({
    certificados: [
      {
        nombre: "Base de Datos UNL",
        url: require("@/assets/certificates/Certificado_BD_UNL.jpg"),
      },
      {
        nombre: "DevtHub UNL",
        url: require("@/assets/certificates/Certificado_DevtHub_UNL.jpg"),
      },
      {
        nombre: "DevtHub",
        url: require("@/assets/certificates/Certificado_DevtHub.jpg"),
      },
      {
        nombre: "NodoCia",
        url: require("@/assets/certificates/Certificado_NodoCia.jpg"),
      },
    ],
    zoom: false,
    url_zoom: null,
  }),
  methods: {
    ver_certificado(index) {
      this.zoom = true;
      this.url_zoom = this.certificados[index].url;
    },
  },
};
</script>

<style>
.ver-imagen {
  cursor: pointer;
}
</style>
