<template>
  <v-app>
    <v-app-bar
      v-if="$store.state.scrollY > 0"
      class="centrar-flex"
      fixed
      elevation="12"
      style="background: rgba(0, 0, 0, 0.822)"
    >
      <v-list class="menu-nav-bar">
        <v-list-item
          v-for="(item, index) in opciones"
          :key="index"
          class="menu-nav-bar-item"
          :title="item.title"
          @click="go_to_section(item.id)"
        >
          <v-list-item-title v-if="!$store.state.is_movil">
            {{ item.tipo }}
          </v-list-item-title>
          <v-list-item-icon v-else style="margin: auto; padding: 0 5px">
            <v-icon style="color: inherit">{{ item.icon }}</v-icon>
          </v-list-item-icon>
        </v-list-item>
      </v-list>
    </v-app-bar>

    <v-main>
      <v-container fluid style="padding: 0; margin: 0">
        <section>
          <v-img :src="require('@/assets/img/fondo.jpg')" height="100vh">
            <v-container class="centrar-flex text-center" style="height: 100vh">
              <v-row>
                <v-col cols="12">
                  <img
                    :src="require('@/assets/img/dev.gif')"
                    alt="Gif Developer"
                    height="250px"
                    style="border-radius: 50%"
                  />
                </v-col>
                <v-col cols="12">
                  <div class="letras">
                    <span style="--i: 1">H</span>
                    <span style="--i: 2">O</span>
                    <span style="--i: 3">L</span>
                    <span style="--i: 4">A</span>
                    <span style="--i: 5">,</span>
                    <br />
                    <span style="--i: 6">S</span>
                    <span style="--i: 7">O</span>
                    <span style="--i: 8">Y</span>
                    <br />
                    <span style="--i: 9">A</span>
                    <span style="--i: 10">L</span>
                    <span style="--i: 11">E</span>
                    <span style="--i: 12">X</span>
                    <span style="--i: 13">I</span>
                    <span style="--i: 14">S</span>
                  </div>
                </v-col>
              </v-row>
            </v-container>
          </v-img>
        </section>
      </v-container>
      <v-container fluid style="padding: 0; margin: 0">
        <div class="fondo-info">
          <v-container class="text-center" :fluid="!$store.state.is_movil">
            <Info />
            <Tecnologias />
            <Certificados />
            <Proyectos />
          </v-container>
        </div>
      </v-container>
    </v-main>
    <div v-if="$store.state.scrollY > 0">
      <v-btn
        @click="volver_inicio()"
        class="btn-inicio"
        fab
        title="¿Volver al inicio?"
        :small="$store.state.is_movil"
      >
        <v-icon :large="!$store.state.is_movil" color="blue"
          >mdi-arrow-up-bold</v-icon
        >
      </v-btn>
    </div>
    <v-footer class="text-center d-flex flex-column" color="#000000e8" dark>
      <div>
        <v-btn
          v-for="(item, i) in redes"
          :key="item.icon + i"
          color="white"
          small
          text
          fab
          style="margin: 0 5px"
          :title="item.title"
          @click="abrir_link(item.link)"
        >
          <v-icon>{{ item.icon }}</v-icon>
        </v-btn>
      </div>
      <v-divider></v-divider>
      <div>
        {{ new Date().getFullYear() }}
      </div>
      <v-divider></v-divider>
      <div><strong>Alexis Armijos</strong></div>
    </v-footer>
  </v-app>
</template>

<script>
import Info from "@/views/fragments/Info.vue";
import Tecnologias from "@/views/fragments/Tecnologias.vue";
import Proyectos from "@/views/fragments/Proyectos.vue";
import Certificados from "@/views/fragments/Certificados.vue";

export default {
  name: "HomePage",
  components: {
    Info,
    Tecnologias,
    Proyectos,
    Certificados,
  },
  data: () => ({
    opciones: [
      {
        tipo: "Información",
        id: "Informacion",
        icon: "mdi mdi-information",
        title: "¿Ver Información Personal?",
      },
      {
        tipo: "Tecnologías",
        id: "Tecnologias",
        icon: "mdi mdi-monitor-cellphone-star",
        title: "¿Ver Tecnologías de Desarrollo?",
      },
      {
        tipo: "Certificados",
        id: "Certificados",
        icon: "mdi mdi-file-certificate-outline",
        title: "¿Ver Certificados?",
      },
      {
        tipo: "Proyectos",
        id: "Proyectos",
        icon: "mdi mdi-folder-star-multiple-outline",
        title: "¿Ver Proyectos?",
      },
      // {
      //   tipo: "Contacto",
      //   id: "Contacto",
      //   icon: "mdi mdi-contacts",
      //   title: "¿Ver Contacto?",
      // },
    ],
    redes: [
      // {
      //   icon: "mdi mdi-facebook",
      //   link: "https://www.facebook.com/Ales550/",
      //   title: "¿Ver Perfil en Facebook?",
      // },
      {
        icon: "mdi mdi-instagram",
        link: "https://www.instagram.com/yo.de.yopi/",
        title: "¿Ver Perfil en Instagram?",
      },
      {
        icon: "mdi mdi-whatsapp",
        link: "https://wa.link/cb1rpb",
        title: "¿Enviar Mensaje por Whatsapp?",
      },
    ],
  }),
  methods: {
    abrir_link(link) {
      window.open(link, "_blank");
    },
    get_size() {
      this.$store.state.width = window.innerWidth;
      this.$store.state.height = window.innerHeight;
      if (window.innerWidth < 960) {
        this.$store.state.is_movil = true;
      } else {
        this.$store.state.is_movil = false;
      }
    },
    get_scrollY() {
      this.$store.state.scrollY = window.scrollY;
    },
    go_to_section(id) {
      document.getElementById(id).scrollIntoView({ behavior: "smooth" });
    },
    volver_inicio() {
      window.scrollTo({
        top: 0,
        behavior: "smooth",
      });
    },
  },
  mounted() {
    this.get_size();
    window.addEventListener("resize", this.get_size);
    window.addEventListener("scroll", this.get_scrollY);
  },
  beforeDestroy() {
    window.addEventListener("resize", this.get_size);
    window.addEventListener("scroll", this.get_scrollY);
  },
};
</script>

<style>
.fondo-info {
  background-image: url("@/assets/img/fondo6.jpg") !important;
  background-attachment: fixed !important;
  background-position: center !important;
  background-repeat: no-repeat !important;
  background-size: cover !important;
}

.section-info {
  background: rgb(1, 3, 14);
  background: linear-gradient(
    180deg,
    rgba(1, 3, 14, 1) 0%,
    rgba(110, 130, 241, 1) 100%
  );
}

.menu-nav-bar {
  display: flex;
  cursor: pointer;
  background-color: transparent !important;
  padding: 0;
}
.menu-nav-bar-item {
  color: white !important;
  padding: 0 10px;
  margin: 0 10px;
  text-align: center;
}
.menu-nav-bar-item:hover {
  background-color: #ffffff3b;
  border-radius: 5px;
}

/* CSS Botón Volver al inicio */
.btn-inicio {
  background: transparent;
  position: fixed;
  bottom: 15px;
  right: 15px;
  animation: rebote 0.5s alternate infinite ease-in;
  z-index: 10;
}

@keyframes rebote {
  0% {
    transform: translateY(-10%);
  }
}

/* CSS Letras */
.letras {
  -webkit-box-reflect: below -20px linear-gradient(transparent, rgba(0, 0, 0, 0.2));
  font-size: 40px;
}
.letras span {
  font-family: "Alfa Slab One", cursive;
  display: inline-block;
  color: #fff;
  text-transform: uppercase;
  animation: letras 1s infinite;
  animation-delay: calc(0.1s * var(--i));
}
@keyframes letras {
  0%,
  40%,
  100% {
    transform: translateY(0);
  }
  20% {
    transform: translateY(-20px);
  }
}
</style>
